import { Drawer, Toolbar, Box, Typography } from '@mui/material';

const drawerWidth = 240;

export default function Sidebar() {
  return (
    <Drawer
      variant="permanent"
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: {
          width: drawerWidth,
          boxSizing: 'border-box',
        },
      }}
    >
      <Toolbar />
      <Box sx={{ overflow: 'auto', p: 2 }}>
        <Typography>My Drive</Typography>
        <Typography>Shared with Me</Typography>
        <Typography>Trash</Typography>
      </Box>
    </Drawer>
  );
}